import InfoCustomNav from '../../components/info-custom-nav.vue'
import InfoSwiper from '../../components/info-swiper.vue'
export default {
	name: 'HouseInfo',
	components: {
		InfoCustomNav,
		InfoSwiper
	},
	data() {
		return {
			info: null,
			stateTip: '联系房主',
			scrollTop: 0,
			isHouse: false,
			isCar: false,
			isStorage: false,
			configs: [
				{
					logo: '视',
					name: '电视机',
					isInclude: false
				},
				{
					logo: '微',
					name: '微波炉',
					isInclude: false
				},
				{
					logo: '暖',
					name: '暖气',
					isInclude: false
				},
				{
					logo: '洗',
					name: '洗衣机',
					isInclude: false
				},
				{
					logo: '热',
					name: '热水器',
					isInclude: false
				},
				{
					logo: '阳',
					name: '阳台',
					isInclude: false
				},
				{
					logo: '空',
					name: '空调',
					isInclude: false
				},
				{
					logo: '柜',
					name: '衣柜',
					isInclude: false
				},
				{
					logo: '冰',
					name: '冰箱',
					isInclude: false
				},
				{
					logo: '燃',
					name: '燃气灶',
					isInclude: false
				},
				{
					logo: 'W',
					name: 'WIFI',
					isInclude: false
				},
				{
					logo: '卫',
					name: '独卫',
					isInclude: false
				}
			]
		}
	},
	methods: {
		getData () {
			this.$http({
				url: this.$urlPath.getRentingHouseInfo,
				params: {
					hid: this.$routeParams.hid
				},
				onRequestSuccess: (res) => {
					if (res.data) {
						this.info = res.data
						//0=审核,1=上架,2=下架,3=已售出,4=审核失败,
						switch (this.info.status) {
							case 0:
								this.stateTip = '审核中'
							break
							case 1:
								this.stateTip = '联系房主'
							break
							case 2:
								this.stateTip = '已下架'
								uni.showModal({
									content: '该房源已下架，查看其它的吧~',
									success: (res) => {
										if (res.confirm) {
											this.$back()
										}
									}
								})
							break
							case 3:
								this.stateTip = '已租出'
								uni.showModal({
									content: '该房源已租出，查看其它的吧~',
									success: (res) => {
										if (res.confirm) {
											this.$back()
										}
									}
								})
							break
						}
						this.info.images = this.info.images.split(',')
						this.isHouse = this.info.house_type === 0
						this.isCar = this.info.house_type === 1
						this.isStorage = this.info.house_type === 2
						if (this.info.house_info) {
							this.info.house_info = JSON.parse(this.info.house_info)
							const infoConfigs = this.info.house_info.configs
							this.configs.forEach(it => {
								it.isInclude = infoConfigs.some(i => i.indexOf(it.logo) !== -1)
							})
						}
					}
				},
				onRequestFail: (errorCode, error) => {
					this.$toast(error)
				}
			})
		}
	},
	onLoad() {
		this.getData()
	},
	onPageScroll(e) {
		this.scrollTop = e.scrollTop
	}
}
